import { ToastService } from './../../../../_services/_toast/_toast.service';
import { Component, OnInit } from '@angular/core';
import { Constants } from '../../../../constants';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { DbService } from 'src/app/_services';

@Component({
  templateUrl: './add-product.component.html',
  styleUrls: ['./add-product.component.css'],
})
export class AddProductComponent implements OnInit {
  constructor(
    public constants: Constants,
    private formBuilder: FormBuilder,
    private db: DbService,
    private toastService: ToastService
  ) {}

  productForm: FormGroup;
  productTypes: any;

  ngOnInit() {
    this.initForm();
    this.getProductTypes();
  }

  private async getProductTypes(): Promise<void> {
    this.productTypes = await this.db.getProductTypes();
  }

  private initForm() {
    this.productForm = this.formBuilder.group({
      name: [[], [Validators.required]],
      productTypeId: [[], [Validators.required]],
      code: [[], [Validators.required]],
      price: [[], [Validators.required]],
    });
  }

  get productTypeId() {
    return this.productForm.get('productTypeId').value;
  }
  get name() {
    return this.productForm.get('name').value;
  }
  get code() {
    return this.productForm.get('code').value;
  }
  get price() {
    return this.productForm.get('price').value;
  }

  public tryAddProduct() {
    if (this.productForm.valid) {
      this.addProduct();
    } else {
      this.toastService.showError('Missing required fields');
    }
  }

  private async addProduct() {
    const product = this.productForm.value;
    try {
      await this.db.addProduct(product);
      this.toastService.showSuccess(`Product '${product.name}' added successfully`);
    } catch (error) {
      this.toastService.showError(error);
    }
  }
}
